<script>
import draggable from "vuedraggable";

export default {
  name: "marktable",
  props: {
    change: Function,
    getQID: Function,
    data: {
      Text: String,
      Des: String,
      ValueArray: Array,
      Topic: Array,
    },
  },
  data: function () {
    return {
      scoreVal: 5,
      editable: false,
      edititem: {},
      Des: "",
    };
  },
  methods: {
    handleScoreColumn() {
      let ori = this.data.ValueArray.length;
      const score = this.scoreVal;
      console.log(ori, score);
      if (ori >= score) {
        this.data.ValueArray.splice(score, ori - score);
        return;
      }
      while (ori < score) {
        this.data.ValueArray.push({
          Value: this.data.ValueArray.length + 1,
          Text: "Text",
        });
        ori = this.data.ValueArray.length;
      }
    },
    coverty() {
      this.data.Des = this.Des.replace(/[\n\r]/g, "<br/>");
    },
    focusable(e) {
      e.target.select();
    },
  },
  components: {
    draggable,
  },
  computed: {
    updateRate() {
      // vm.data.Des = `${this.data.Des.split("<br/>")[0]}<br/>1. ${this.data.ValueArray[0].Text} <br/>2. ${this.data.ValueArray[1].Text} <br/>3. ${this.data.ValueArray[2].Text} <br/>4. ${this.data.ValueArray[3].Text}<br/>5. ${this.data.ValueArray[4].Text}`
      return "";
    },
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
